export function getParentContainer(element: HTMLElement): HTMLElement | null {
  return element.closest('[data-dropin-container]') as HTMLElement | null;
}

export function scrollToElement(element: HTMLElement) {
  element.focus();

  if (document.activeElement !== element) {
    element.scrollIntoView({ behavior: 'smooth' });
  }
}

export function scrollToParentContainer(element: HTMLElement) {
  const container = getParentContainer(element);

  if (container) {
    scrollToElement(container);
  }
}
